import { createRouter, createWebHistory } from 'vue-router'
import store from '../store'

// Ïª¥Ìè¨ÎÑåÌä∏ Ï†ïÏ†Å import (GitHub Pages Ìò∏Ìôò)
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import AuthCallback from '../views/AuthCallback.vue'
import Trading from '../views/Trading.vue'
import NotFound from '../views/NotFound.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { 
      title: 'Ìôà - AI Í∏∞Î∞ò ÏûêÎèôÎß§Îß§',
      description: 'Ïã§ÏãúÍ∞Ñ Îâ¥Ïä§ Î∂ÑÏÑùÍ≥º AI ÏöîÏïΩÏúºÎ°ú Ïä§ÎßàÌä∏Ìïú Ìà¨ÏûêÎ•º ÏãúÏûëÌïòÏÑ∏Ïöî',
      requiresAuth: false 
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { 
      title: 'Î°úÍ∑∏Ïù∏ - AI Í∏∞Î∞ò ÏûêÎèôÎß§Îß§',
      description: 'Google ÎòêÎäî Kakao Í≥ÑÏ†ïÏúºÎ°ú Í∞ÑÌé∏ÌïòÍ≤å Î°úÍ∑∏Ïù∏ÌïòÏÑ∏Ïöî',
      requiresAuth: false 
    }
  },
  {
    path: '/trading',
    name: 'Trading',
    component: Trading,
    meta: { 
      title: 'ÏûêÎèôÎß§Îß§ - ÌïúÍµ≠Ìà¨ÏûêÏ¶ùÍ∂å Ïó∞Îèô',
      description: 'AI Î∂ÑÏÑùÍ≥º Ï†ÑÎ¨∏ Îß§Îß§ Ï†ÑÎûµÏúºÎ°ú ÏûêÎèôÌôîÎêú Ï£ºÏãù Ìà¨Ïûê',
      requiresAuth: true 
    }
  },
  {
    path: '/auth/callback',
    name: 'AuthCallback',
    component: AuthCallback,
    meta: { 
      title: 'Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨ Ï§ë...',
      description: 'ÏÜåÏÖú Î°úÍ∑∏Ïù∏ÏùÑ Ï≤òÎ¶¨ÌïòÍ≥† ÏûàÏäµÎãàÎã§',
      requiresAuth: false 
    }
  },
  {
    path: '/404',
    name: 'NotFound',
    component: NotFound,
    meta: { 
      title: 'ÌéòÏù¥ÏßÄÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§ - AI Í∏∞Î∞ò ÏûêÎèôÎß§Îß§',
      description: 'ÏöîÏ≤≠ÌïòÏã† ÌéòÏù¥ÏßÄÍ∞Ä Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏäµÎãàÎã§',
      requiresAuth: false 
    }
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: '/404'
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    // Ï†ÄÏû•Îêú Ïä§ÌÅ¨Î°§ ÏúÑÏπòÍ∞Ä ÏûàÏúºÎ©¥ Î≥µÏõê
    if (savedPosition) {
      return savedPosition
    }
    // ÏïµÏª§ ÎßÅÌÅ¨Í∞Ä ÏûàÏúºÎ©¥ Ìï¥Îãπ ÏúÑÏπòÎ°ú Ïä§ÌÅ¨Î°§
    if (to.hash) {
      return {
        el: to.hash,
        behavior: 'smooth'
      }
    }
    // Í∏∞Î≥∏Ï†ÅÏúºÎ°ú ÌéòÏù¥ÏßÄ ÏÉÅÎã®ÏúºÎ°ú Ïä§ÌÅ¨Î°§
    return { top: 0 }
  }
})

// ÎùºÏö∞ÌÑ∞ Í∞ÄÎìú
router.beforeEach(async (to, from, next) => {
  console.log('üîÑ ÎùºÏö∞ÌÑ∞ Ïù¥Îèô:', from.path, '‚Üí', to.path)
  
  // Î°úÎî© ÏÉÅÌÉú ÏãúÏûë
  store.dispatch('setLoading', true)
  
  // ÌéòÏù¥ÏßÄ Î©îÌÉÄ Ï†ïÎ≥¥ ÏÑ§Ï†ï
  if (to.meta.title) {
    document.title = to.meta.title
  }
  
  if (to.meta.description) {
    let metaDescription = document.querySelector('meta[name="description"]')
    if (!metaDescription) {
      metaDescription = document.createElement('meta')
      metaDescription.setAttribute('name', 'description')
      document.head.appendChild(metaDescription)
    }
    metaDescription.setAttribute('content', to.meta.description)
  }
  
  // Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌïú ÌéòÏù¥ÏßÄ Ï≤¥ÌÅ¨
  if (to.meta.requiresAuth) {
    console.log('üîí Ïù∏Ï¶ù ÌïÑÏöîÌïú ÌéòÏù¥ÏßÄ Ï†ëÍ∑º ÏãúÎèÑ')
    
    const isAuthenticated = store.getters['auth/isAuthenticated']
    
    if (!isAuthenticated) {
      console.log('‚ùå Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùå - Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô')
      
      store.dispatch('setLoading', false)
      
      next({
        name: 'Login',
        query: { 
          redirect: to.fullPath,
          message: 'login_required'
        }
      })
      return
    }
    
    // ÌÜ†ÌÅ∞Ïù¥ ÏûàÏßÄÎßå ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Í∞Ä ÏóÜÎäî Í≤ΩÏö∞ ÌîÑÎ°úÌïÑ Î°úÎìú ÏãúÎèÑ
    if (!store.getters['auth/user']) {
      console.log('üîÑ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Î°úÎî© Ï§ë...')
      
      try {
        await store.dispatch('auth/loadUserProfile')
        console.log('‚úÖ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Î°úÎìú ÏôÑÎ£å')
      } catch (error) {
        console.error('‚ùå ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Î°úÎìú Ïã§Ìå®:', error)
        
        // ÌÜ†ÌÅ∞Ïù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ Î°úÍ∑∏ÏïÑÏõÉ Ï≤òÎ¶¨
        await store.dispatch('auth/logout')
        
        store.dispatch('setLoading', false)
        
        next({
          name: 'Login',
          query: { 
            redirect: to.fullPath,
            message: 'session_expired'
          }
        })
        return
      }
    }
  }
  
  // Ïù¥ÎØ∏ Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©ÏûêÍ∞Ä Î°úÍ∑∏Ïù∏/ÏΩúÎ∞± ÌéòÏù¥ÏßÄÏóê Ï†ëÍ∑ºÌïòÎäî Í≤ΩÏö∞
  if ((to.name === 'Login' || to.name === 'AuthCallback') && store.getters['auth/isAuthenticated']) {
    console.log('‚úÖ Ïù¥ÎØ∏ Î°úÍ∑∏Ïù∏Îê® - Ï†ÅÏ†àÌïú ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô')
    
    store.dispatch('setLoading', false)
    
    // Î¶¨Îã§Ïù¥Î†âÌä∏ Í≤ΩÎ°úÍ∞Ä ÏûàÏúºÎ©¥ Ìï¥Îãπ Í≤ΩÎ°úÎ°ú, ÏóÜÏúºÎ©¥ ÌôàÏúºÎ°ú
    const redirectPath = to.query.redirect || '/'
    
    if (redirectPath === to.path) {
      next({ name: 'Home' })
    } else {
      next(redirectPath)
    }
    return
  }
  
  // ÌäπÏ†ï Í≤ΩÎ°úÏóêÏÑú ÌäπÏ†ï Í≤ΩÎ°úÎ°úÏùò Ïù¥Îèô Ï†úÌïú (Î¨¥Ìïú Î£®ÌîÑ Î∞©ÏßÄ)
  if (from.name === 'Login' && to.name === 'Login') {
    console.log('‚ö†Ô∏è Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄ Î¨¥Ìïú Î£®ÌîÑ Î∞©ÏßÄ')
    store.dispatch('setLoading', false)
    next({ name: 'Home' })
    return
  }
  
  store.dispatch('setLoading', false)
  next()
})

// ÎùºÏö∞ÌÑ∞ ÏóêÎü¨ Ï≤òÎ¶¨
router.afterEach((to, from, failure) => {
  // Î°úÎî© ÏÉÅÌÉú Ï¢ÖÎ£å
  store.dispatch('setLoading', false)
  
  if (failure) {
    console.error('‚ùå ÎùºÏö∞ÌÑ∞ Ïù¥Îèô Ïã§Ìå®:', failure)
    
    // ÌäπÏ†ï Ïò§Î•òÏóê ÎåÄÌïú Ï≤òÎ¶¨
    if (failure.type === 'aborted') {
      console.log('‚ö†Ô∏è ÎùºÏö∞ÌÑ∞ Ïù¥ÎèôÏù¥ Ï§ëÎã®Îê®')
    } else if (failure.type === 'cancelled') {
      console.log('‚ö†Ô∏è ÎùºÏö∞ÌÑ∞ Ïù¥ÎèôÏù¥ Ï∑®ÏÜåÎê®')
    } else if (failure.type === 'duplicated') {
      console.log('‚ö†Ô∏è ÎèôÏùºÌïú ÎùºÏö∞Ìä∏Î°ú Ïù¥Îèô ÏãúÎèÑ')
    }
  } else {
    console.log('‚úÖ ÎùºÏö∞ÌÑ∞ Ïù¥Îèô ÏôÑÎ£å:', to.path)
    
    // ÌéòÏù¥ÏßÄ Î∞©Î¨∏ Ï∂îÏ†Å (Google Analytics Îì±) - gtagÍ∞Ä Ï†ïÏùòÎêú Í≤ΩÏö∞Îßå
    if (typeof window !== 'undefined' && window.gtag) {
      window.gtag('config', 'GA_TRACKING_ID', {
        page_path: to.path,
        page_title: to.meta.title
      })
    }
  }
})

// ÎùºÏö∞ÌÑ∞ Ïò§Î•ò Í∏ÄÎ°úÎ≤å Ìï∏Îì§Îü¨
router.onError((error) => {
  console.error('üí• ÎùºÏö∞ÌÑ∞ Í∏ÄÎ°úÎ≤å Ïò§Î•ò:', error)
  
  // Ïò§Î•ò Ï†ïÎ≥¥Î•º Ïä§ÌÜ†Ïñ¥Ïóê Ï†ÄÏû•
  store.dispatch('setError', {
    message: 'ÌéòÏù¥ÏßÄ Î°úÎî© Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.',
    details: error.message
  })
  
  // Î°úÎî© ÏÉÅÌÉú Ï¢ÖÎ£å
  store.dispatch('setLoading', false)
})

export default router