            {
              key: 'long_period',
              label: 'Ïû•Í∏∞ Ïù¥ÎèôÌèâÍ∑†',
              type: 'number',
              min: 20,
              max: 120,
              step: 1,
              placeholder: '20',
              description: 'Ïû•Í∏∞ Ïù¥ÎèôÌèâÍ∑† Í∏∞Í∞Ñ (Ïùº)'
            },
            {
              key: 'volume_ratio',
              label: 'Í±∞ÎûòÎüâ Î∞∞Ïàò',
              type: 'number',
              min: 1.0,
              max: 5.0,
              step: 0.1,
              placeholder: '1.5',
              description: 'ÌèâÍ∑† Í±∞ÎûòÎüâ ÎåÄÎπÑ Î∞∞Ïàò'
            }
          ]
        },
        {
          id: 'rsi_strategy',
          name: 'RSI Ïó≠Ï∂îÏÑ∏',
          icon: 'üîÑ',
          description: 'RSI Í≥ºÎß§ÎèÑ/Í≥ºÎß§Ïàò Íµ¨Í∞ÑÏóêÏÑú Ïó≠Ï∂îÏÑ∏Î•º ÎÖ∏Î¶¨Îäî Ï†ÑÎûµ',
          expectedReturn: '10-15%',
          riskLevel: 'ÎÜíÏùå',
          parameters: [
            {
              key: 'rsi_period',
              label: 'RSI Í∏∞Í∞Ñ',
              type: 'number',
              min: 7,
              max: 21,
              step: 1,
              placeholder: '14',
              description: 'RSI Í≥ÑÏÇ∞ Í∏∞Í∞Ñ (Ïùº)'
            },
            {
              key: 'oversold',
              label: 'Í≥ºÎß§ÎèÑ Í∏∞Ï§Ä',
              type: 'number',
              min: 20,
              max: 35,
              step: 1,
              placeholder: '30',
              description: 'RSI Í≥ºÎß§ÎèÑ Í∏∞Ï§ÄÍ∞í'
            },
            {
              key: 'overbought',
              label: 'Í≥ºÎß§Ïàò Í∏∞Ï§Ä',
              type: 'number',
              min: 65,
              max: 80,
              step: 1,
              placeholder: '70',
              description: 'RSI Í≥ºÎß§Ïàò Í∏∞Ï§ÄÍ∞í'
            }
          ]
        },
        {
          id: 'bollinger_strategy',
          name: 'Î≥ºÎ¶∞Ï†Ä Î∞¥Îìú',
          icon: 'üéØ',
          description: 'Î≥ºÎ¶∞Ï†Ä Î∞¥Îìú ÏàòÏ∂ï ÌõÑ ÌôïÏû• ÏãúÏ†êÏùÑ Ìè¨Ï∞©ÌïòÎäî Ï†ÑÎûµ',
          expectedReturn: '12-18%',
          riskLevel: 'Ï§ëÍ∞Ñ',
          parameters: [
            {
              key: 'bb_period',
              label: 'Î≥ºÎ¶∞Ï†Ä Í∏∞Í∞Ñ',
              type: 'number',
              min: 10,
              max: 30,
              step: 1,
              placeholder: '20',
              description: 'Î≥ºÎ¶∞Ï†Ä Î∞¥Îìú Í≥ÑÏÇ∞ Í∏∞Í∞Ñ (Ïùº)'
            },
            {
              key: 'bb_deviation',
              label: 'ÌëúÏ§ÄÌé∏Ï∞®',
              type: 'number',
              min: 1.5,
              max: 2.5,
              step: 0.1,
              placeholder: '2.0',
              description: 'ÌëúÏ§ÄÌé∏Ï∞® Î∞∞Ïàò'
            },
            {
              key: 'squeeze_threshold',
              label: 'ÏàòÏ∂ï Í∏∞Ï§Ä',
              type: 'number',
              min: 0.1,
              max: 0.5,
              step: 0.01,
              placeholder: '0.2',
              description: 'Î∞¥Îìú ÏàòÏ∂ï ÌåêÎã® Í∏∞Ï§Ä'
            }
          ]
        }
      ],
      
      selectedStrategy: null,
      strategyParams: {},
      
      // Ï¢ÖÎ™© Îç∞Ïù¥ÌÑ∞
      recommendedStocks: [],
      selectedStocks: [],
      loadingStocks: false,
      stockKeyword: '',
      searchResults: [],
      
      // Îß§Îß§ Ï†úÏñ¥
      tradingStatus: 'stopped', // stopped, running, paused
      investAmount: 1000000,
      allocationMethod: 'equal',
      stopLoss: 5,
      takeProfit: 10,
      
      // Ìè¨ÏßÄÏÖòÍ≥º Í∏∞Î°ù
      positions: [],
      tradingHistory: [],
      historyPeriod: 'today',
      
      // ÏóÖÎç∞Ïù¥Ìä∏ Ïù∏ÌÑ∞Î≤å
      updateTimer: null
    }
  },
  
  computed: {
    ...mapGetters('auth', ['isAuthenticated']),
    
    canStart() {
      return this.selectedStrategy && 
             this.selectedStocks.length > 0 && 
             this.investAmount >= 10000 &&
             this.investAmount <= this.accountInfo.balance &&
             this.tradingStatus === 'stopped'
    }
  },
  
  async mounted() {
    if (!this.isAuthenticated) {
      this.$router.push('/login')
      return
    }
    
    await this.initialize()
  },
  
  beforeUnmount() {
    if (this.updateTimer) {
      clearInterval(this.updateTimer)
    }
  },
  
  methods: {
    async initialize() {
      try {
        await Promise.all([
          this.refreshAccount(),
          this.getRecommendations(),
          this.refreshPositions(),
          this.loadHistory()
        ])
      } catch (error) {
        console.error('Ï¥àÍ∏∞Ìôî Ïã§Ìå®:', error)
        this.$toast.error('ÏãúÏä§ÌÖú Ï¥àÍ∏∞ÌôîÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      }
    },
    
    async refreshAccount() {
      try {
        const response = await apiClient.get('/trading/account')
        this.accountInfo = response.data.data
      } catch (error) {
        console.error('Í≥ÑÏ¢å Ï°∞Ìöå Ïã§Ìå®:', error)
        this.$toast.error('Í≥ÑÏ¢å Ï†ïÎ≥¥Î•º Ï°∞ÌöåÌï† Ïàò ÏóÜÏäµÎãàÎã§.')
      }
    },
    
    async getRecommendations() {
      this.loadingStocks = true
      try {
        const response = await apiClient.get('/trading/ai-recommendations')
        this.recommendedStocks = response.data.data
      } catch (error) {
        console.error('AI Ï∂îÏ≤ú Ïã§Ìå®:', error)
        this.$toast.error('AI Ï¢ÖÎ™© Ï∂îÏ≤úÏùÑ Î∞õÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.')
      } finally {
        this.loadingStocks = false
      }
    },
    
    selectStrategy(strategy) {
      this.selectedStrategy = strategy
      this.strategyParams = {}
      
      strategy.parameters.forEach(param => {
        this.strategyParams[param.key] = param.placeholder
      })
    },
    
    toggleStock(stockCode) {
      const index = this.selectedStocks.indexOf(stockCode)
      if (index > -1) {
        this.selectedStocks.splice(index, 1)
      } else {
        this.selectedStocks.push(stockCode)
      }
    },
    
    async searchStocks() {
      if (!this.stockKeyword.trim()) return
      
      try {
        const response = await apiClient.get('/trading/search-stock', {
          params: { keyword: this.stockKeyword }
        })
        this.searchResults = response.data.data
      } catch (error) {
        console.error('Ï¢ÖÎ™© Í≤ÄÏÉâ Ïã§Ìå®:', error)
        this.$toast.error('Ï¢ÖÎ™© Í≤ÄÏÉâÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      }
    },
    
    addStock(stock) {
      if (!this.recommendedStocks.find(s => s.code === stock.code)) {
        this.recommendedStocks.push({
          ...stock,
          aiReason: 'ÏÇ¨Ïö©Ïûê ÏßÅÏ†ë Ï∂îÍ∞Ä',
          aiScore: 50
        })
      }
      this.toggleStock(stock.code)
      this.searchResults = []
      this.stockKeyword = ''
    },
    
    async startTrading() {
      if (!this.canStart) return
      
      try {
        const config = {
          strategy: this.selectedStrategy.id,
          strategyParams: this.strategyParams,
          stocks: this.selectedStocks,
          investmentAmount: this.investAmount,
          allocationMethod: this.allocationMethod,
          stopLoss: this.stopLoss,
          takeProfit: this.takeProfit
        }
        
        await apiClient.post('/trading/start', config)
        this.tradingStatus = 'running'
        this.$toast.success('ÏûêÎèôÎß§Îß§Í∞Ä ÏãúÏûëÎêòÏóàÏäµÎãàÎã§!')
        
        this.startAutoUpdate()
      } catch (error) {
        console.error('ÏûêÎèôÎß§Îß§ ÏãúÏûë Ïã§Ìå®:', error)
        this.$toast.error('ÏûêÎèôÎß§Îß§ ÏãúÏûëÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      }
    },
    
    async stopTrading() {
      try {
        await apiClient.post('/trading/stop')
        this.tradingStatus = 'stopped'
        this.$toast.success('ÏûêÎèôÎß§Îß§Í∞Ä Ï§ëÏßÄÎêòÏóàÏäµÎãàÎã§.')
        
        this.stopAutoUpdate()
      } catch (error) {
        console.error('ÏûêÎèôÎß§Îß§ Ï§ëÏßÄ Ïã§Ìå®:', error)
        this.$toast.error('ÏûêÎèôÎß§Îß§ Ï§ëÏßÄÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      }
    },
    
    async pauseTrading() {
      try {
        await apiClient.post('/trading/pause')
        this.tradingStatus = 'paused'
        this.$toast.info('ÏûêÎèôÎß§Îß§Í∞Ä ÏùºÏãúÏ†ïÏßÄÎêòÏóàÏäµÎãàÎã§.')
      } catch (error) {
        console.error('ÏùºÏãúÏ†ïÏßÄ Ïã§Ìå®:', error)
        this.$toast.error('ÏùºÏãúÏ†ïÏßÄÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      }
    },
    
    async refreshPositions() {
      try {
        const response = await apiClient.get('/trading/positions')
        this.positions = response.data.data
      } catch (error) {
        console.error('Ìè¨ÏßÄÏÖò Ï°∞Ìöå Ïã§Ìå®:', error)
      }
    },
    
    async loadHistory() {
      try {
        const response = await apiClient.get('/trading/history', {
          params: { filter: this.historyPeriod }
        })
        this.tradingHistory = response.data.data
      } catch (error) {
        console.error('Í∏∞Î°ù Ï°∞Ìöå Ïã§Ìå®:', error)
      }
    },
    
    async sellAll(position) {
      if (!confirm(`${position.name} Ï†ÑÎüâÏùÑ Îß§ÎèÑÌïòÏãúÍ≤†ÏäµÎãàÍπå?`)) return
      
      try {
        await apiClient.post('/trading/sell-position', {
          stockCode: position.code,
          quantity: position.quantity
        })
        this.$toast.success(`${position.name} Îß§ÎèÑ Ï£ºÎ¨∏Ïù¥ Ï†ëÏàòÎêòÏóàÏäµÎãàÎã§.`)
        await this.refreshPositions()
      } catch (error) {
        console.error('Îß§ÎèÑ Ïã§Ìå®:', error)
        this.$toast.error('Îß§ÎèÑ Ï£ºÎ¨∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      }
    },
    
    startAutoUpdate() {
      this.updateTimer = setInterval(() => {
        this.refreshPositions()
        this.refreshAccount()
      }, 30000)
    },
    
    stopAutoUpdate() {
      if (this.updateTimer) {
        clearInterval(this.updateTimer)
        this.updateTimer = null
      }
    },
    
    getStatusText() {
      const statusMap = {
        stopped: 'Ï§ëÏßÄÎê®',
        running: 'Ïã§Ìñâ Ï§ë',
        paused: 'ÏùºÏãúÏ†ïÏßÄ'
      }
      return statusMap[this.tradingStatus] || 'Ïïå Ïàò ÏóÜÏùå'
    },
    
    getTradeStatus(status) {
      const statusMap = {
        pending: 'ÎåÄÍ∏∞',
        executed: 'Ï≤¥Í≤∞',
        cancelled: 'Ï∑®ÏÜå',
        failed: 'Ïã§Ìå®'
      }
      return statusMap[status] || status
    },
    
    formatMoney(amount) {
      if (typeof amount !== 'number') return '0'
      return amount.toLocaleString('ko-KR')
    },
    
    formatTime(timestamp) {
      return new Date(timestamp).toLocaleString('ko-KR', {
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      })
    }
  }
